.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
}

html, body {
    height: 100%;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #09d3ac;
}

.experienceBar {
    border-right: 1px solid lightgrey;
}

.experienceBarNotch {
    border: 3px solid white;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    background: #58cd58;
}

.content {
    border: 1px solid lightgrey;
    background: white;
    border-radius: 4px;
    cursor: pointer;
    color: black;
}

.content:hover {
    box-shadow: 0px 1px 6px 1px #58cd58;
}
.content h6{
    margin-bottom:0;
    text-overflow: ellipsis;
    overflow: hidden;
    top:4px;
    position: relative;
}
.content p {
   text-align: left;
    text-overflow: ellipsis;
    overflow: hidden;
}

.bar2, .bar4 {
    right: 10px !important;
    position: relative;
}

.bar1, .bar3 {
    left: 10px;
    position: relative;
}

.aboutContainer .list-group-item:hover {
    background: #cde8ff;
}

.animateName {
    position: relative;
    width: 12em;
    margin: 0 auto;
    border-right: 2px solid rgba(255,255,255,.75);
    text-align: center;
    white-space: nowrap;
    overflow: hidden;
    letter-spacing: 0.25em;
    transform: translateY(-50%);
    animation: typewriter 4s steps(18) 2s infinite alternate both,
    blinkTextCursor 500ms steps(18) infinite normal;
}
.animateName:hover{
    -webkit-animation-play-state: paused; /* Safari 4.0 - 8.0 */
    animation-play-state: paused;
}
@keyframes typewriter{
     from{width: 0;}
     to{width: 14em;}
 }
@keyframes blinkTextCursor{
    from{border-right-color: rgba(255,255,255,.75);}
    to{border-right-color: transparent;}
}
.content .list-group-item{
    max-height: 18vh;
    overflow: auto;
    text-overflow: ellipsis;
}

.backgroundTheme{
    background: #475056;
    color:white;
}

.profilePic img{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    -webkit-box-shadow: 0px 3px 2px 2px rgb(195, 206, 195);
    -moz-box-shadow: 0px 3px 2px 2px rgb(195, 206, 195);
    box-shadow: 0px 3px 2px 2px rgb(195, 206, 195);
}
.infoContainer{
    background: white;
    color:black;
   width:24vw;
    border-top:4px solid #58cd58;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border-left: 1px solid #475056;
    border-right:1px solid #475056;
}
.infoSection{
    align-items: flex-end;
    height: 100vh;
}
.fold {

    padding: 10px;
    position: relative;
    text-decoration: none;
    color: #999;
    background: #fff;
    transition: all ease 0.5s;
}
.fold:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    border-style: solid;
    border-width: 0 49px 100px 0;
    border-color: #ddd #1d2b39;
    transition: all ease 0.5s;
}